.Content{
    margin: 32px 0;
    padding: 0 16px;
}

.Content div{
    margin-left: auto;
    margin-right: auto;
    max-width: 65rem;
}

.Content h1{
    color: #010101;
}

.Content blockquote{
    margin: 0;
    padding: 0 0 0 15px;
    height: 40px;
    border-left: 10px solid #c0c0c0;
    line-height: 25px;
    font-size: 15px;
}

.Content blockquote p{
    display: block;
    padding: 8px 0;
}

.Content a{
    margin-left: .5rem;
    -webkit-box-shadow: 0 1px 0 0 currentColor;
            box-shadow: 0 1px 0 0 currentColor;
    color: #007acc;
    text-decoration: none;
    cursor: pointer;
}

.Content a:hover{
    -webkit-box-shadow: none;
            box-shadow: none;
}

.Content ul{
    padding-left: 16px;
}

.Content li{
    margin-top: 16px;
}

p{
    color: #17252a;
    line-height: 30px;
    font-size: 17px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

@media (min-width: 769px){
   .Content{
        margin: 32px 8rem;
        padding: 0 24px;
   }
   
   .Content div{
    margin: 0 100px;
    padding: 0 24px;
}
}


