.Content{
    overflow: hidden;
    margin: 0;
    padding: 0 16px;
}

.Content div{
    max-width: 65rem;
}

.Button{
    width: 30%;
    background-color: transparent;
    cursor: pointer;
    color: #010101;
    font: inherit;
    line-height: 39px;
    font-size: 25px;
    margin:25px 0;
    padding-bottom: 5px;
    font-weight: bold;
    border-bottom: 1px solid #c0c0c0;
    text-shadow: 1px 1px #c0c0c0;
}

@media (min-width: 769px){
    .Content{
         margin: 16px 10rem;
         padding: 0 24px;
    }
 }


