.Content{
    overflow: hidden;
    margin: 32px 0;
    padding: 0 16px;
}

.Content div{
    max-width: 65rem;
}

.Article{
    overflow: hidden;
	padding: 20px 0;
    border-bottom: 1px solid #dcdcdc;
}

.Title{
    line-height: 27px;
    font-size: 18px;
    font-weight: bold;
    color: #333;
}

.Desc{
    line-height: 24px;
    font-size: 13px;
    color: #999;
}

@media (min-width: 769px){
    .Content{
         margin: 32px 10rem;
         padding: 0 24px;
    }
 }


