.SideDrawer {
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 200;
    background-color: white;
    padding: 32px 16px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -webkit-transition: -webkit-transform 0ms linear .1s;
    transition: -webkit-transform 0ms linear .1s;
    -o-transition: transform 0ms linear .1s;
    transition: transform 0ms linear .1s;
    transition: transform 0ms linear .1s, -webkit-transform 0ms linear .1s;
}

@media (min-width: 769px) {
    .SideDrawer {
        display: none;
    }
}

.Open {
    -webkit-transform: translateY(0);
        -ms-transform: translateY(0);
            transform: translateY(0);
}

.Close {
    -webkit-transform: translateY(-100%);
        -ms-transform: translateY(-100%);
            transform: translateY(-100%);
}


